import React from 'react'
import ExternalLink from './ExternalLink'

interface PillProps {
    text: string
}

const Pill: React.FC<PillProps> = ({ text }) => {
    return <span className="pill">{text}</span>
}

interface ProjectEntryProps {
    title: string
    url?: string
    linkTitle?: string
    pills?: string[]
    description?: string
}

const ProjectEntry: React.FC<ProjectEntryProps> = ({
    title,
    url,
    linkTitle,
    pills,
    description,
}) => {
    return (
        <div className="project-entry">
            <div>
                <h3 className="title">{title}</h3>
                {url && (
                    <ExternalLink href={url}>{linkTitle || url}</ExternalLink>
                )}
            </div>
            {pills.map((text, index) => (
                <Pill key={index} text={text} />
            ))}
            <div>{description}</div>
        </div>
    )
}

export default ProjectEntry
